import React, { useState, useEffect, useRef } from 'react';
import { app, db, auth } from './firebase'; // Import the initialized Firestore instance
import { signInWithEmailAndPassword } from 'firebase/auth';
import Header from "./Header";
import Footer from "./Footer";
import Note from "./Note";
import CreateArea from "./CreateArea";
import { addDoc, collection, deleteDoc, doc, getDocs } from 'firebase/firestore'; // Import Firestore functions
import './styles.css'; // Adjust the path as per your file structure

function App() {
  const notesCollectionRef = collection(db, "notes");
  const [notes, setNotes] = useState([]);
  const [user, setUser] = useState(null); // State to hold authenticated user
  const [email, setEmail] = useState(''); // State to hold email input value
  const [password, setPassword] = useState(''); // State to hold password input value
  const [error, setError] = useState(null);

  // Function to fetch notes from Firestore
  async function fetchNotes() {
    try {
      const querySnapshot = await getDocs(notesCollectionRef);
      const fetchedNotes = [];
      querySnapshot.forEach((doc) => {
        // Push each note document with its ID into the fetchedNotes array
        fetchedNotes.push({ id: doc.id, ...doc.data() });
      });
      setNotes(fetchedNotes); // Update local state with fetched notes
    } catch (error) {
      console.error("Error fetching notes: ", error);
    }
  }

  useEffect(() => {
    // Fetch notes from Firestore when the component mounts
    fetchNotes();
  }, []); // Empty dependency array ensures useEffect runs only once after initial render

  async function addNote(newNote) {
    // Add the new note to Firestore first
    try {
      // Update the local state only if the Firestore operation is successful
      const docRef = await addDoc(notesCollectionRef, newNote);
      console.log("Document written with ID: ", docRef.id);

      // Add the Firestore document ID to the note
      const noteWithId = { ...newNote, id: docRef.id };
      setNotes((prevNotes) => {
        return [...prevNotes, noteWithId];
      });
    } catch (e) {
      console.error("Error adding document: ", e);
    }
  }

  //----------------------------------------------------------------
  async function deleteNote(id) {
    // Assume each note has a unique Firestore document ID stored as note.id
    const noteDocRef = doc(db, "notes", id);
    console.log("Deleting this ID: ", noteDocRef.id);

    try {
      // Delete the document from Firestore
      await deleteDoc(noteDocRef);
      console.log("Document successfully deleted!");

      // Update the local state if the Firestore operation is successful
      setNotes((prevNotes) => {
        return prevNotes.filter((noteItem) => {
          return noteItem.id !== id;
        });
      });
    } catch (e) {
      console.error("Error deleting document: ", e);
    }
  }
  // ----------------------------------------------------------------
  // login
  // Login handler
  const handleLogin = async (e) => {
    e.preventDefault();
    try {
      const userCredential = await signInWithEmailAndPassword(auth, email, password);
      const user = userCredential.user;
      setUser(user); // Set the authenticated user state
      setError(null); // Clear any previous errors
    } catch (error) {
      setError(error.message);
    }
  };

  // Render login form if user is not authenticated
  if (!user) {
    return (
      <div>
        <Header />
        <div className="login-container">
          <h2>Login</h2>
          <form className="login-form" onSubmit={handleLogin}>
            <input type="email" placeholder="Email" value={email} onChange={(e) => setEmail(e.target.value)} />
            <input type="password" placeholder="Password" value={password} onChange={(e) => setPassword(e.target.value)} />
            <button type="submit">Login</button>
            {error && <p>{error}</p>}
          </form>
        </div>
        <Footer />
      </div>
    );
  }


  //-----
  return (
    <div>
      <Header />
      <CreateArea onAdd={addNote} />
      {notes.map((noteItem, index) => {
        return (
          <Note
            key={index}
            id={noteItem.id.toString()}
            title={noteItem.title}
            content={noteItem.content}
            onDelete={deleteNote}
          />
        );
      })}
      <Footer />
    </div>
  );
}

export default App;
