import React from "react"; // Import React
import DeleteIcon from "@mui/icons-material/Delete"; // Import the DeleteIcon component from MUI icons
import PropTypes from 'prop-types'; // Import PropTypes for prop validation

function Note(props) { // Define the Note component
  function handleClick() { // Function to handle click event on the delete button
    props.onDelete(props.id); // Call the onDelete function passed via props with the note's id
  }

  return ( // JSX to render the note
    <div className="note"> {/* Note container */}
      <h1>{props.title}</h1> {/* Display the note's title */}
      <p>{props.content}</p> {/* Display the note's content */}
      <button onClick={handleClick}> {/* Button to delete the note */}
        <DeleteIcon /> {/* Delete icon inside the button */}
      </button>
    </div>
  );
}
Note.propTypes = {
  id: PropTypes.string.isRequired, // Ensure id is a string and is required
  title: PropTypes.string.isRequired, // Ensure title is a string and is required
  content: PropTypes.string.isRequired, // Ensure content is a string and is required
  onDelete: PropTypes.func.isRequired // Ensure onDelete is a function and is required
};
export default Note; // Export the Note component as the default export
