import React, { useState } from "react"; // Import React and the useState hook for state management
import AddIcon from "@mui/icons-material/Add"; // Import the AddIcon component from Material UI
import { Fab } from "@mui/material"; // Import the Fab (Floating Action Button) component from Material UI
import { Zoom } from "@mui/material"; // Import the Zoom component from Material UI

function CreateArea(props) { // Define the CreateArea component and receive props
    const [isExpanded, setExpanded] = useState(false); // State to track if the input area is expanded

    const [note, setNote] = useState({ // State to track the note being created
        title: "", // Initialize title as an empty string
        content: "", // Initialize content as an empty string
    });

    function handleChange(event) { // Function to handle changes in the input fields
        const { name, value } = event.target; // Destructure name and value from the event target

        setNote((prevNote) => { // Update the note state
            return {
                ...prevNote, // Spread the previous note properties
                [name]: value, // Update the property (title or content) with the new value
            };
        });
    }

    function submitNote(event) { // Function to handle form submission
        props.onAdd(note); // Call the onAdd prop (passed from parent) with the current note
        setNote({ // Reset the note state to empty values
            title: "",
            content: "",
        });
        event.preventDefault(); // Prevent the default form submission behavior
    }

    function expand() { // Function to expand the input area
        setExpanded(true); // Set isExpanded state to true
    }

    return ( // JSX to render the component
        <div>
            <form className="create-note"> {/* Form for creating a note */}
                {isExpanded && ( // Conditionally render the title input field if isExpanded is true
                    <input
                        name="title" // Name attribute for identifying the input field
                        onChange={handleChange} // Event handler for changes in the input field
                        value={note.title} // Value of the input field from state
                        placeholder="Title" // Placeholder text for the input field
                    />
                )}

                <textarea
                    name="content" // Name attribute for identifying the textarea
                    onClick={expand} // Event handler to expand the input area on click
                    onChange={handleChange} // Event handler for changes in the textarea
                    value={note.content} // Value of the textarea from state
                    placeholder="Take a note..." // Placeholder text for the textarea
                    rows={isExpanded ? 3 : 1} // Rows attribute to set height based on isExpanded state
                />
                <Zoom in={isExpanded}>
                    {/* Zoom effect for the Fab button  */}
                    <Fab onClick={submitNote}> {/* Floating Action Button to submit the note */}
                        <AddIcon /> {/* Add icon inside the Fab button */}
                    </Fab>
                </Zoom>
            </form>
        </div>
    );
}

export default CreateArea; // Export the CreateArea component as the default export
